import React, { createContext, useContext, useState, useCallback } from "react";
import hash from "object-hash";
import storage from "../../utils/Storage";
const StorageContext = createContext({
    storageHash: "",
    onGetResults: () => ({}),
    onSetResults: () => { },
    onRemoveResults: () => { }
});
export const RESULTS_KEY_NAME = "ResultKey";
const StorageContextProvider = ({ children }) => {
    const [storageHash, setStorageHash] = useState(hash(storage.get(RESULTS_KEY_NAME)));
    const onGetResults = useCallback(() => {
        return storage.get(RESULTS_KEY_NAME);
    }, []);
    const onSetResults = useCallback((data) => {
        storage.set(RESULTS_KEY_NAME, data);
        setStorageHash(hash(data));
    }, []);
    const onRemoveResults = useCallback(() => {
        storage.remove(RESULTS_KEY_NAME);
        setStorageHash("");
    }, []);
    const value = {
        storageHash,
        onGetResults,
        onSetResults,
        onRemoveResults
    };
    return (React.createElement(StorageContext.Provider, { value: value }, children));
};
const useStorage = () => useContext(StorageContext);
export { StorageContextProvider, useStorage };
