const diacriticsMap = {
    "a": "(a|á)", // (a|á|à|â|ä|ã|å|ā)
    "o": "(o|ó)", // oóòôöõøō
    "e": "(e|é)", // eéèêëē
    "i": "(i|í)", // iíìîïī
    "u": "(u|ú)", // uúùûüū
    "n": "(n|ñ)"
};
const replaceAllDiacriticsChars = (query) => {
    const diacriticsKeys = Object.keys(diacriticsMap);
    let result = query.toLowerCase();
    diacriticsKeys.forEach((key) => {
        result = result.replaceAll(key, `${diacriticsMap[key].replace("(", "(?:")}`);
    });
    return result;
};
export const getQuerySearchRe = (query) => {
    // console.log(new RegExp(`(\\b${replaceAllDiacriticsChars(query)}\\b)`, "giu"));
    return new RegExp(`(\\b${replaceAllDiacriticsChars(query)}\\b)`, "giu");
};
function getTooltip(match, translation) {
    return `<span class="${styles.tooltip}">${match}<span class="${styles.tooltipText}">${translation}</span></span>`;
}
function highlightText(text, query, translation) {
    if (!query)
        return text;
    const regex = getQuerySearchRe(query);
    return text.replaceAll(regex, getTooltip("$1", translation));
}
