import React, { createContext, useContext, useState, useCallback } from "react";
import toast from "react-hot-toast";
// import { responseData } from "./mockedText";
import { useStorage } from "../Storage";
const initialData = {
    text: "",
    // simplifiedText: parsedStoredData.adaptedText || "",
    translations: [],
    // dictionary: parsedStoredData.dictionary || {},
    // dictionary: {},
    exersizes: {},
    isLoading: false,
    // onSetText: () => {},
    onStart: () => { },
    onClear: () => { }
};
const ContentContext = createContext(initialData);
// function mockFetch() {
//     return new Promise((res, rej) => {
//         // setTimeout(() => rej("Oops!"), 2000);
//         setTimeout(() => res(responseData), 10000);
//     });
// }
const ContentContextProvider = ({ children }) => {
    const storage = useStorage();
    const savedData = storage.onGetResults();
    // console.log(savedData, " savedData");
    const [text, setText] = useState(savedData.text || initialData.text);
    // const [simplifiedText, setSimplifiedText] = useState(initialData.simplifiedText);
    const [translations, setTranslations] = useState(savedData.translations || initialData.translations);
    // const [dictionary, setDictionary] = useState(initialData.dictionary || savedData.text);
    const [exersizes, setExersizes] = useState(savedData.exersizes || initialData.exersizes);
    const [isLoading, setIsLoading] = useState(initialData.isLoading);
    // const onSetText = useCallback((event: ChangeEvent<HTMLTextAreaElement>) => {
    //     setText(event.target.value);
    // }, []);
    const onClear = useCallback(() => {
        setText("");
        setTranslations([]);
        // setDictionary({});
        setExersizes({});
        storage.onRemoveResults();
    }, []);
    const onStart = useCallback(async ({ sourceLanguage, targetLanguage, topic, level }) => {
        onClear();
        setIsLoading(true);
        try {
            const response = await fetch(`${process.env.PUBLIC_URL}/api/adapt-text`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({
                    // text: text ? text.trim() : undefined,
                    sourceLanguage,
                    targetLanguage,
                    topic,
                    level
                })
            });
            const data = await response.json();
            // const data = await mockFetch();
            // console.log(data);
            setText(data.text);
            // setSimplifiedText(data.adaptedText);
            setTranslations(data.translations);
            // setDictionary(data.dictionary);
            setExersizes(data.exersizes);
            storage.onSetResults({ ...data, sourceLanguage, targetLanguage, topic, level });
        }
        catch (error) {
            console.log(error);
            toast.error("Something went wrong, please try again later");
        }
        setIsLoading(false);
    }, []);
    const value = {
        text,
        // simplifiedText,
        translations,
        // dictionary,
        exersizes,
        isLoading,
        // onSetText,
        onStart,
        onClear
    };
    return (React.createElement(ContentContext.Provider, { value: value }, children));
};
const useContent = () => useContext(ContentContext);
export { ContentContextProvider, useContent };
