import React, { useCallback, useMemo } from "react";
import Select from "react-select";
import styles from "./styles.css";
import { useFilters } from "../../context/Filters";
import { useContent } from "../../context/Content";
import SubmitButton from "../SubmitButton";
import { AvailableCerfLevels } from "../../types";
import { availableLanguages } from "../../constants/languages";
import { topicsEnOptions } from "../../constants/topics";
const cerfLevelsOptions = Object.values(AvailableCerfLevels)
    .map((item) => ({ label: item, value: item }));
function Filters() {
    const { sourceLanguage, targetLanguage, level, topic, 
    // size,
    onChangeFilters } = useFilters();
    const { isLoading } = useContent();
    const sourceLanguageValue = useMemo(() => {
        return availableLanguages.find((option) => option.value === sourceLanguage);
    }, [sourceLanguage]);
    const targetLanguageValue = useMemo(() => {
        return availableLanguages.find((option) => option.value === targetLanguage);
    }, [targetLanguage]);
    const topicValue = useMemo(() => {
        return topicsEnOptions.find((option) => option.value === topic);
    }, [topic]);
    const cerfLevelValue = useMemo(() => {
        return cerfLevelsOptions.find((option) => option.value === level);
    }, [level]);
    const onChangeSourceLanguage = useCallback((option) => {
        onChangeFilters({ sourceLanguage: option.value });
    }, [onChangeFilters]);
    const onChangeTargetLanguage = useCallback((option) => {
        onChangeFilters({ targetLanguage: option.value });
    }, [onChangeFilters]);
    const onChangeTopic = useCallback((option) => {
        onChangeFilters({ topic: option.value });
    }, [onChangeFilters]);
    const onChangeLevel = useCallback((option) => {
        onChangeFilters({ level: option.value });
    }, [onChangeFilters]);
    // const onChangeSize = useCallback((event: ChangeEvent<HTMLInputElement>) => {
    //     onChangeFilters({ size: event.target.value });
    // }, [onChangeFilters]);
    // const onChangeTopic = useCallback((event) => {
    //     onChangeFilters({ topic: event.target.value });
    // }, [onChangeFilters]);
    return (React.createElement("div", { className: styles.container },
        React.createElement(Select, { value: sourceLanguageValue, placeholder: "Source Lang", options: availableLanguages, isDisabled: isLoading, className: styles.select, onChange: onChangeSourceLanguage }),
        React.createElement(Select, { value: targetLanguageValue, placeholder: "Target Lang", options: availableLanguages, isDisabled: isLoading, className: styles.select, onChange: onChangeTargetLanguage }),
        React.createElement(Select, { value: topicValue, placeholder: "Select topic", options: topicsEnOptions, isDisabled: isLoading, className: styles.select, onChange: onChangeTopic }),
        React.createElement(Select, { value: cerfLevelValue, placeholder: "Select level", options: cerfLevelsOptions, isDisabled: isLoading, className: styles.select, onChange: onChangeLevel }),
        React.createElement(SubmitButton, null)));
}
export default Filters;
