import React, { createContext, useContext, useState, useCallback, useEffect, useMemo } from "react";
import { history } from "./utils";
import { useStorage } from "../Storage";
const initSearchParams = new URLSearchParams(history.location.search);
const initialFilters = {
    sourceLanguage: initSearchParams.get("sourceLanguage"),
    targetLanguage: initSearchParams.get("targetLanguage"),
    level: initSearchParams.get("level"),
    topic: initSearchParams.get("topic")
    // size: initSearchParams.get("size")
};
const FiltersContext = createContext({
    ...initialFilters,
    areFiltersChanged: false,
    onChangeFilters: () => { }
});
const FiltersContextProvider = ({ children }) => {
    const [filters, setFilters] = useState(initialFilters);
    const storage = useStorage();
    const onChangeFilters = useCallback((params) => {
        const searchParams = new URLSearchParams(history.location.search);
        const paramsKeys = Object.keys(params);
        paramsKeys.forEach((key) => {
            let value = params[key];
            if (value) {
                // @ts-ignore
                searchParams.set(key, value);
            }
            else {
                searchParams.delete(key);
            }
        });
        history.replace({
            pathname: history.location.pathname,
            search: searchParams.toString()
        });
        setFilters({
            ...filters,
            ...params
        });
    }, [filters]);
    const areFiltersChanged = useMemo(() => {
        const savedFilters = storage.onGetResults();
        return (filters.sourceLanguage !== savedFilters.sourceLanguage
            || filters.targetLanguage !== savedFilters.targetLanguage
            || filters.topic !== savedFilters.topic
            || filters.level !== savedFilters.level);
    }, [filters, storage.storageHash]);
    useEffect(() => {
        const params = {};
        // if (!filters.size) {
        //     params.size = "medium";
        // }
        if (Object.keys(params).length > 0) {
            onChangeFilters(params);
        }
    }, [filters]);
    const value = {
        sourceLanguage: filters.sourceLanguage,
        targetLanguage: filters.targetLanguage,
        level: filters.level,
        topic: filters.topic,
        areFiltersChanged,
        // size: filters.size,
        onChangeFilters
    };
    return (React.createElement(FiltersContext.Provider, { value: value }, children));
};
const useFilters = () => useContext(FiltersContext);
export { FiltersContextProvider, useFilters };
